import { Link } from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";



import API from "./API";



function Home() { 
    const [data, setData] = useState([]);
    const [error, setError] = useState();
    const [loadingbar, setHideLoading] = useState(false);

    const fetchAPIData = async () => {

        try {
            const res = await API.get("commodities");
            setData(res.data);

            setHideLoading(true);
        }
        catch (error) {
            setError(error);

        }
 

    };

    useEffect(() => {

        fetchAPIData();
    }, []);






    if (!data) return <> {error}</>;


    if (!loadingbar)


        return (


            "Loading... please wait "
        )


    return (

        <div className="sakshmb">

            <p className="text-center">Minimum order quantity is 10 tonnes</p>


            <table className="table  table-auto  ">


                <tbody>

                    {data.map((item, index) => (


                        <tr key={index}>
                            <td>   {item.label} </td>

                            <td >   Seller deals available:-{item.deal_count}
                            </td>
                            <td>

                                <Link className="btn btn-outline-success" to={`commodity/${item.label}`}>View</Link>



                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>


        </div>

    )
}

export default Home;